import './scss/courseReact.scss'
import Back from '../components/Back'
const CourseReact = () => {    
    const coursesTitle = [
        {
            title: 'Giới thiệu',
            courseContent: [
                {
                    titleSub: 'ReactJS là gì? Tại sao nên học React js'
                },
                {
                    titleSub: 'SPA/MPA là gì?'
                }
            ]
        },
        {
            title: 'Ôn lại ES6+',
            courseContent: [
                {
                    titleSub: 'Arrow function'
                },
                {
                    titleSub: 'Javascript modules'
                },
                {
                    titleSub: 'Enhanced object literals'
                },
                {
                    titleSub: 'Spread operator'
                }
                ,
                {
                    titleSub: 'Destructuring'
                }
            ]

        },
        {
            title: 'React, React-DOM',
            courseContent: [
                {
                    titleSub: 'document.createElement() để làm gì?'
                },
                {
                    titleSub: 'ReactJS là gì? Tại sao nên học React js2'
                },
                {
                    titleSub: 'React.createElement() nữa là sao?'
                },
                {
                    titleSub: 'React-DOM là gì? Tại sao phải sử dụng?'
                }
            ]

        },
        {
            title: 'JSX, Components, Props',
            courseContent: [
                {
                    titleSub: 'JSX là gì? Tại sao cần JSX?'
                },
                {
                    titleSub: 'JSX render Arrays | JSX FQA'
                },
                {
                    titleSub: 'React element types'
                },
                {
                    titleSub: 'Props là gì? Dùng props khi nào?'
                },
                {
                    titleSub: 'DOM events?'
                },
                {
                    titleSub: 'Quy ước đặt tên components?'
                },
                {
                    titleSub: 'Children props? Render props?'
                },
            ]
        },
        {
            title: 'Create React App',
            courseContent: [
                {
                    titleSub: 'NodeJS là gì? Tại sao phải sử dụng NodeJS?'
                },
                {
                    titleSub: 'Tạo dự án với React + Webpack'
                },
                {
                    titleSub: 'Create React App'
                },
                {
                    titleSub: 'NPM, NPX và YARN là gì?'
                },
                {
                    titleSub: 'CRA Folder Structure'
                }
            ]
        },
        {
            title: 'Hooks',
            courseContent: [
                {
                    titleSub: 'Hooks là gì?'
                },
                {
                    titleSub: 'useState hooks'
                },
                {
                    titleSub: 'Two-way binding'
                },
                {
                    titleSub: 'Todo list with useState'
                },
                {
                    titleSub: 'Mounted & Unmounted?'
                },
                {
                    titleSub: 'useEffect hook'
                },
                {
                    titleSub: 'useEffect with dependencies'
                },
                {
                    titleSub: 'useEffect with DOM events'
                },
                {
                    titleSub: 'useEffect with timer functions'
                },
                {
                    titleSub: 'useEffect with preview avatar'
                },
                {
                    titleSub: 'useEffect with fake Chat App'
                },
                {
                    titleSub: 'useLayoutEffect hook'
                },
                {
                    titleSub: 'useRef hook'
                },
                {
                    titleSub: 'React.memo HOC'
                },
                {
                    titleSub: 'useCallback hook'
                },
                {
                    titleSub: 'useMemo hook'
                },
                {
                    titleSub: 'useReducer hook'
                },
                {
                    titleSub: 'Todo App with useReducer hook'
                },
                {
                    titleSub: 'useReducer recap'
                },
                {
                    titleSub: 'useContext hook'
                },
                {
                    titleSub: 'Context + useReducer'
                },
                {
                    titleSub: 'useImperativeHandle hook'
                }
            ]
        },
        {
            title: 'CSS, SCSS và CSS modules',
            courseContent: [
                {
                    titleSub: 'Sử dụng CSS'
                },
                {
                    titleSub: 'CSS modules'
                },
                {
                    titleSub: 'Thư viện clsx và classnames'
                },
                {
                    titleSub: 'Install SASS để dùng SCSS'
                }
            ]
        },
        {
            title: 'React Router V6',
            courseContent: [
                {
                    titleSub: 'Cài đặt và tạo router'
                }
            ]
        },
    ]
    const handleClickShow = (e) => {         
        const nextElement = e.target.nextSibling
        console.log(nextElement)
        nextElement.style.height = 0
        nextElement.style.overflow = 'hidden'
        if (nextElement.style.height === 0) {
            nextElement.style.height = 'fit-content'            
            console.log(nextElement.style.height === 0);
        }else {
            nextElement.style.height = 0;
            nextElement.style.overflow = 'hidden';
        }
    }
    return (
        <>
            <h1 className='title'>Course React</h1>
            <Back />
            <div className="container">
                <div className="content">
                    <h1>Content</h1>
                </div>
                <div className="navbar">
                    {coursesTitle.map(function (course, index) {
                        return (
                            <ul key={index}>
                                <li onClick={handleClickShow}>{`Phần ${index + 1}: ${course.title}`}</li>
                                <ul>
                                    {course.courseContent.map((courseContent, index) => {
                                        return (
                                            <li key={index}>{`${index + 1}. ${courseContent.titleSub}`}</li>
                                        )
                                    })}
                                </ul>
                            </ul>
                        )
                    })}
                </div>
            </div>
        </>
    )
}

export default CourseReact
